import React, { useState, useEffect } from 'react';
import { AiOutlineClose, AiOutlineMenu } from 'react-icons/ai';
import { motion, AnimatePresence } from 'framer-motion';

const Navbar = () => {
    const [nav, setNav] = useState(false);
    const [activeSection, setActiveSection] = useState('home');
    const [scrolled, setScrolled] = useState(false);

    const handleNav = () => {
        setNav(!nav);
    };

    const smoothScrollTo = (elementId) => {
        const element = document.getElementById(elementId);
        if (element) {
            element.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            });
        }
        setNav(false);
    };

    useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 50);
            
            const sections = ['home', 'technologies', 'about', 'projects'];
            const scrollPosition = window.scrollY + 100;
            
            for (const section of sections) {
                const element = document.getElementById(section);
                if (element) {
                    const { offsetTop, offsetHeight } = element;
                    if (scrollPosition >= offsetTop && scrollPosition < offsetTop + offsetHeight) {
                        setActiveSection(section);
                        break;
                    }
                }
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    return (
        <motion.div 
            className={`fixed w-full top-0 left-0 z-50 transition-all duration-500 ${
                scrolled ? 'glass-morphism-dark backdrop-blur-md' : 'glass-morphism'
            }`}
            initial={{ y: -100, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8, ease: 'easeOut' }}
        >
            <div className='flex justify-between items-center h-20 max-w-7xl mx-auto px-6 text-white'>
                <motion.h1 
                    className='text-2xl font-bold bg-gradient-to-r from-cyan-300 to-blue-400 bg-clip-text text-transparent drop-shadow-lg cursor-pointer'
                    onClick={() => smoothScrollTo('home')}
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                >
                    Alex Kim
                </motion.h1>
                <ul className='hidden md:flex space-x-8'>
                    {['home', 'technologies', 'about', 'projects'].map((section) => (
                        <li key={section}>
                            <motion.button
                                onClick={() => smoothScrollTo(section)}
                                className={`font-medium transition-all duration-300 relative ${
                                    activeSection === section ? 'text-blue-300' : 'text-white hover:text-blue-300'
                                }`}
                                whileHover={{ scale: 1.1 }}
                                whileTap={{ scale: 0.95 }}
                            >
                                {section.charAt(0).toUpperCase() + section.slice(1)}
                                {activeSection === section && (
                                    <motion.div
                                        className="absolute -bottom-1 left-0 right-0 h-0.5 bg-gradient-to-r from-cyan-300 to-blue-400 rounded-full"
                                        layoutId="activeSection"
                                        transition={{ type: "spring", stiffness: 380, damping: 30 }}
                                    />
                                )}
                            </motion.button>
                        </li>
                    ))}
                </ul>
                <motion.div 
                    onClick={handleNav} 
                    className='block md:hidden cursor-pointer p-2'
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                >
                    <AnimatePresence mode="wait">
                        {nav ? (
                            <motion.div
                                key="close"
                                initial={{ rotate: -90, opacity: 0 }}
                                animate={{ rotate: 0, opacity: 1 }}
                                exit={{ rotate: 90, opacity: 0 }}
                                transition={{ duration: 0.2 }}
                            >
                                <AiOutlineClose size={20} />
                            </motion.div>
                        ) : (
                            <motion.div
                                key="menu"
                                initial={{ rotate: 90, opacity: 0 }}
                                animate={{ rotate: 0, opacity: 1 }}
                                exit={{ rotate: -90, opacity: 0 }}
                                transition={{ duration: 0.2 }}
                            >
                                <AiOutlineMenu size={20} />
                            </motion.div>
                        )}
                    </AnimatePresence>
                </motion.div>
                <AnimatePresence>
                    {nav && (
                        <motion.div 
                            className='fixed left-0 top-0 w-[70%] h-full glass-morphism-dark z-50 backdrop-blur-xl'
                            initial={{ x: "-100%", opacity: 0 }}
                            animate={{ x: 0, opacity: 1 }}
                            exit={{ x: "-100%", opacity: 0 }}
                            transition={{ type: "spring", stiffness: 300, damping: 30 }}
                        >
                            <motion.h1 
                                className='text-2xl font-bold bg-gradient-to-r from-cyan-300 to-blue-400 bg-clip-text text-transparent drop-shadow-lg m-6 cursor-pointer'
                                onClick={() => smoothScrollTo('home')}
                                whileHover={{ scale: 1.05 }}
                            >
                                Alex Kim
                            </motion.h1>
                            <ul className='p-6 space-y-4'>
                                {['home', 'technologies', 'about', 'projects'].map((section, index) => (
                                    <motion.li 
                                        key={section}
                                        className='p-4 border-b border-white/20'
                                        initial={{ x: -50, opacity: 0 }}
                                        animate={{ x: 0, opacity: 1 }}
                                        transition={{ delay: index * 0.1 + 0.2 }}
                                    >
                                        <motion.button
                                            onClick={() => smoothScrollTo(section)}
                                            className={`text-white hover:text-blue-300 transition-colors duration-300 font-medium w-full text-left ${
                                                activeSection === section ? 'text-blue-300' : ''
                                            }`}
                                            whileHover={{ scale: 1.05, x: 10 }}
                                            whileTap={{ scale: 0.95 }}
                                        >
                                            {section.charAt(0).toUpperCase() + section.slice(1)}
                                        </motion.button>
                                    </motion.li>
                                ))}
                            </ul>
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>
        </motion.div>
    );
};

export default Navbar;
