import React from 'react';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

// Technology icons
import reactIcon from '../assets/react.svg';
import pytorchIcon from '../assets/pytorch.svg';
import scikitIcon from '../assets/scikit-learn.svg';
import pandasIcon from '../assets/Pandas.svg';
import postgresqlIcon from '../assets/PostgresSQL.svg';
import pythonIcon from '../assets/Python.svg';
import latexIcon from '../assets/LaTeX.svg';
import rstudioIcon from '../assets/RStudio.svg';
import cppIcon from '../assets/C++ (CPlusPlus).svg';
import javaIcon from '../assets/Java.svg';
import opencvIcon from '../assets/opencv.svg';
import jsIcon from '../assets/JavaScript.svg';
import tailwindIcon from '../assets/Tailwind CSS.svg'
import htmlIcon from '../assets/html.svg';

const Technologies = () => {
  const [titleRef, titleInView] = useInView({ threshold: 0.3, triggerOnce: true });
  const [gridRef, gridInView] = useInView({ threshold: 0.1, triggerOnce: true });

  const technologies = [
    { name: 'React.js', icon: reactIcon },
    { name: 'PyTorch', icon: pytorchIcon },
    { name: 'OpenCV', icon: opencvIcon},
    { name: 'Scikit-Learn', icon: scikitIcon },
    { name: 'Pandas', icon: pandasIcon },
    { name: 'PostgreSQL', icon: postgresqlIcon },
    { name: 'Python', icon: pythonIcon },
    { name: 'LaTeX', icon: latexIcon },
    { name: 'R Studio', icon: rstudioIcon },
    { name: 'C++', icon: cppIcon },
    { name: 'Java', icon: javaIcon },
    { name: 'HTML', icon: htmlIcon},
    { name: 'JavaScript', icon: jsIcon },
    { name: 'Tailwind CSS', icon: tailwindIcon}
  ];

  return (
    <div className="py-20 relative overflow-hidden">
      {/* Animated background elements */}
      <motion.div
        className="absolute top-20 right-20 w-64 h-64 bg-gradient-to-r from-green-500/10 to-blue-500/10 rounded-full blur-3xl"
        animate={{
          scale: [1, 1.2, 1],
          rotate: [0, 180, 360],
          x: [0, 30, 0],
          y: [0, -20, 0]
        }}
        transition={{
          duration: 18,
          repeat: Infinity,
          ease: "linear"
        }}
      />
      <motion.div
        className="absolute bottom-20 left-20 w-80 h-80 bg-gradient-to-r from-purple-500/10 to-cyan-500/10 rounded-full blur-2xl"
        animate={{
          scale: [1.2, 1, 1.2],
          rotate: [360, 180, 0],
          x: [0, -25, 0],
          y: [0, 15, 0]
        }}
        transition={{
          duration: 22,
          repeat: Infinity,
          ease: "linear"
        }}
      />

      <motion.div 
        className='w-full h-px bg-gradient-to-r from-transparent via-white/30 to-transparent my-16'
        initial={{ scaleX: 0 }}
        whileInView={{ scaleX: 1 }}
        viewport={{ once: true }}
        transition={{ duration: 1.5, ease: "easeInOut" }}
      />
      
      <div className='max-w-7xl mx-auto px-6 relative z-10'>
        <motion.div 
          ref={titleRef}
          className='text-center mb-12'
          initial={{ opacity: 0, y: 50 }}
          animate={titleInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.8, ease: "easeOut" }}
        >
          <motion.h2 
            className='text-4xl md:text-5xl font-bold mb-4 text-white'
            initial={{ opacity: 0, scale: 0.8 }}
            animate={titleInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: 0.8, delay: 0.2, ease: "easeOut" }}
          >
            Favorite <motion.span 
              className='bg-gradient-to-r from-cyan-300 to-blue-400 bg-clip-text text-transparent drop-shadow-lg'
              initial={{ opacity: 0, rotateY: -90 }}
              animate={titleInView ? { opacity: 1, rotateY: 0 } : {}}
              transition={{ duration: 0.8, delay: 0.4, ease: "easeOut" }}
            >
              Technologies
            </motion.span>
          </motion.h2>
        </motion.div>

        <div className='grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-8 max-w-6xl mx-auto' ref={gridRef}>
          {technologies.map((tech, index) => (
            <motion.div 
              key={index} 
              className='flex flex-col items-center glass-morphism p-6 rounded-2xl group cursor-pointer relative overflow-hidden'
              initial={{ opacity: 0, y: 50, rotateY: -30 }}
              animate={gridInView ? { opacity: 1, y: 0, rotateY: 0 } : {}}
              transition={{ 
                duration: 0.6, 
                delay: index * 0.1 + 0.3,
                type: "spring",
                stiffness: 300,
                damping: 30
              }}
              whileHover={{ 
                scale: 1.1,
                rotateY: 10,
                boxShadow: "0 20px 40px rgba(59, 130, 246, 0.2)",
                zIndex: 10
              }}
              style={{ transformStyle: 'preserve-3d' }}
            >
              <motion.div
                className="absolute inset-0 bg-gradient-to-br from-blue-500/10 to-purple-500/10"
                initial={{ opacity: 0 }}
                whileHover={{ opacity: 1 }}
                transition={{ duration: 0.3 }}
              />
              
              {/* Floating particles on hover */}
              <motion.div 
                className="absolute inset-0 pointer-events-none"
                initial={{ opacity: 0 }}
                whileHover={{ opacity: 1 }}
              >
                {Array.from({ length: 3 }, (_, i) => (
                  <motion.div
                    key={i}
                    className="absolute w-1 h-1 bg-blue-400 rounded-full"
                    style={{
                      left: `${Math.random() * 100}%`,
                      top: `${Math.random() * 100}%`
                    }}
                    animate={{
                      y: [-10, -30, -10],
                      opacity: [0, 1, 0],
                      scale: [0, 1, 0]
                    }}
                    transition={{
                      duration: 2,
                      delay: i * 0.2,
                      repeat: Infinity,
                      ease: "easeInOut"
                    }}
                  />
                ))}
              </motion.div>
              
              <motion.div 
                className='relative z-10'
                whileHover={{ scale: 1.1, rotateZ: 5 }}
                transition={{ type: "spring", stiffness: 400, damping: 25 }}
              >
                <motion.img 
                  src={tech.icon} 
                  alt={`${tech.name} icon`} 
                  className='w-16 h-16 mb-3' 
                  whileHover={{ 
                    filter: "drop-shadow(0 0 10px rgba(59, 130, 246, 0.8))"
                  }}
                />
              </motion.div>
              <motion.p 
                className='text-sm font-medium text-center text-gray-200 group-hover:text-white transition-colors duration-300 relative z-10'
                whileHover={{ scale: 1.05 }}
              >
                {tech.name}
              </motion.p>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Technologies;